import utils from './utils'

const canvas = document.querySelector('canvas')
const c = canvas.getContext('2d')

canvas.width = innerWidth
canvas.height = innerHeight

const mouse = {
  x: innerWidth / 2,
  y: innerHeight / 2
}

const colors = ['#2185C5', '#7ECEFD', '#FFF6E5', '#FF7F66']
var gravity = 1;
var friction = 0.59;
// Event Listeners
addEventListener("mousemove", (event) => {
  mouse.x = event.clientX;
  mouse.y = event.clientY;
});

addEventListener("resize", () => {
  canvas.width = innerWidth;
  canvas.height = innerHeight;

  init();
});

// Objects
class Ball {
  constructor(x, y, radius, color, dy, dx) {
    this.x = x;
    this.y = y;
    this.dy = dy;
    this.dx = dx;
    this.radius = radius;
    this.color = color;
  }

  draw() {
    c.beginPath();
    c.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false);
    c.fillStyle = this.color;
    c.fill();
    c.stroke();
    c.closePath();
  }
  update() {
    if (this.y + this.radius + this.dy > canvas.height) {
      this.dy = -1 * this.dy * friction;
      // gravity *= 1.5;
    } else {
      this.dy += gravity;
    }
    if (this.x + this.radius > canvas.width || this.x - this.radius < 0) {
      this.dx = -1 * this.dx;
      // gravity *= 1.5;
      // } else {
      //   this.dx += gravity;
    }
    this.x += this.dx;
    this.y += this.dy;
    this.draw();
  }
}

// Implementation
let balls = [];

function init() {
  balls = [];
  for (let i = 0; i < 400; i++) {
    let color = colors[Math.floor(Math.random() * 4)];
    let radius = Math.random() * 22 + 8;
    let x = Math.random() * canvas.width - (radius + 30) + 70;
    let y = Math.random() * canvas.height - radius;
    let dx = Math.random() * 4 - 2;
    balls.push(new Ball(x, y, radius, color, 5, dx));
  }
}

// Animation Loop
function animate() {
  requestAnimationFrame(animate);
  c.fillStyle = "rgba(0,0,0,0.5)";
  c.fillRect(0, 0, canvas.width, canvas.height);

  // c.fillText("HTML CANVAS BOILERPLATE", mouse.x, mouse.y);

  balls.forEach((ball) => {
    ball.update();
  });
}

const button = document.querySelector("button");
const div = document.querySelector(".landing");
button.addEventListener("click", () => {
  div.classList.add("anim");
  init();

  animate();
});

document.addEventListener("click", () => {
  init();
});